import React, { Component } from 'react'
import Link from 'gatsby-link'

class HomePage extends Component {
    render() {
        return (
            <div style={{
                padding: '0.5rem',
                maxWidth: '960px',
                margin: 'auto'
            }}>
                <p>Ipas’s Clinical Updates in Reproductive Health contain up-to-date, evidence-based clinical recommendations on comprehensive abortion care, with new topics and resources added regularly. The Clinical Updates in Reproductive Health provide concise, easy-to-read information about abortion care, combining the latest evidence with lessons learned from health professionals globally to produce relevant clinical recommendations.</p>
                
                <h2>Who should use this resource</h2>
                <p>First published in 2013, the Clinical Updates in Reproductive Health were designed originally as an up-to-date, evidence-based clinical resource for Ipas staff. Over time, the publication has also been of use to:
                
                    <ul>
                        <li>clinicians providing abortion care</li>
                        <li>clinical and public health professionals working on patient care protocols in public health systems and the private sector</li>
                        <li>safe abortion advocates and policymakers creating laws and policies that fulfill women’s and girls’ right to health</li>
                    </ul>
                </p>

                <h2>How to use this resource</h2>
                <p>Use the drop-down menu in the top left corner to find clinical recommendations by topic area. With each recommendation, you’ll see a “last reviewed” date that indicates how recently our experts reviewed all relevant evidence and updated our recommendation accordingly. All our recommendations are reviewed at least annually, updated as necessary, and compiled each year into a PDF for download. Please send requests for print copies to <a href="mailto:clinicalupdates@ipas.org">clinicalupdates@ipas.org</a>.</p>

                <h2><Link to="/making-recommendations">How we make clinical recommendations</Link></h2>
                <h2><Link to="/our-team">Our team</Link></h2>

                <h2>Related tools and resources</h2>
                <p>We regularly update our clinical tools and job aids with evidence and recommendations from the Clinical Updates in Reproductive Health. All are available for download here: <Link to="http://www.ipas.org/HealthProviderResources">www.ipas.org/HealthProviderResources</Link></p>
                
                <h2>Featured topics</h2>
                <div>
                    <ul>
                        <li><Link to="/general/summary-of-ma-regimens">Summary of recommended medical abortion regimens</Link></li>
                        <li><Link to="/general/uterine-evacuation">Uterine evacuation: Replace sharp curettage with aspiration or medications</Link></li>
                        <li><Link to="/before-13/adolescents">Adolescents: Safety and efficacy</Link></li>
                        <li><Link to="/pain-management/paracervical-block">Recommended paracervical anesthesia: paracervical block.asp</Link></li>
                        <li><Link to="/at-or-after-13/de/cervical-prep">Cervical preparation</Link></li>
                    </ul>
                </div> 
            </div>
        )
    }
}

export default HomePage



